import { createRouter, createWebHistory } from "vue-router";
import AboutPage from "./pages/AboutPage.vue";
import MapPage from "./pages/MapPage.vue";

const router = createRouter({
  history: createWebHistory(
    process.env.NODE_ENV === "production" ? "/square-gps/" : "/"
  ),
  routes: [
    { path: "/", redirect: { name: "AboutPage" } },
    {
      path: process.env.NODE_ENV === "production" ? "/square-gps/" : "/",
      name: "AboutPage",
      component: AboutPage,
    },
    {
      path: process.env.NODE_ENV === "production" ? "/square-gps/map" : "/map",
      name: 'MapPage',
      component: MapPage,
    },
  ],
});

export default router;
